//  
const throttle = function (fn, interval) {
  // 
  let last = 0;
  return function () {
    // 
    let now = +new Date();
    //      interval ms
    if (now - last > interval) {
      // 
      last = now;
      //  end
      fn('end')
      // console.log('interval');
    }
  };
}

// ()   
const debounce = function (fn, delay) {
  // 
  let timer = null;
  return function () {
    // 
    if (timer) {
      //  
      clearTimeout(timer)
    }
    //  
    timer = setTimeout(() => {
      // delay ms 
      fn('end')
      // console.log('delay')
    }, delay)
  }
}

// ()   1s
const betterDebounce = function (fn, delay) {
  let last = 0
  let timer = null
  return function () {
    let now = +new Date()
    if ((now - last) < delay) {
      clearTimeout(timer)
      timer = setTimeout(() => {
        fn('end')
        // console.log('delay');
        last = now
      }, delay)
    } else {
      last = now
      fn('auto')
      // console.log(' delay');
    }
  }
}


export default {
  throttle,
  debounce,
  betterDebounce
}