/**
 * 
 * @param {String} tips 
 * @param {String} message 
 * @param {String} type success (default) | warning
 * @param {Boolean} showCancelButton  default:false
 * @param {Boolean} showConfirmButton  default:false
 * @param {Boolean} closeOnClickOverlay  default: false
 * @param {Number} duration  showCancelButtonfalse && showConfirmButtonfalse default:2000
 */
function vConfirmSync (options) {
  return new Promise((resolve, reject) => {
    Confirm({
      ...options,
      onClose (action) {
        if (action === 'confirm') resolve(action)
        else reject(action)
      }
    })
  })
}

export default {
  Confirm,
  vConfirmSync
}
